import { writable } from 'svelte/store';
const store = writable({ visible: false, message: '' });
export default store;
let timeout;
/**
 * Displays a message at the top of the devtools
 * @param message The message to display
 * @param duration The time in milliseconds before it is removed. Defaults to 2000ms.
 */
export function toast(message, duration = 2000) {
    store.set({ visible: true, message });
    if (timeout) {
        window.clearTimeout(timeout);
    }
    timeout = setTimeout(() => {
        store.set({ visible: false, message });
        timeout = null;
    }, duration);
}
