<script>
  import toastStore from './stores/toastStore'
  let message = 'Browser cache cleared.'
  let visible = false

  toastStore.subscribe(value => {
    message = value.message
    visible = value.visible
  })
</script>

<!-- 
  Displays a message at the top of the devtools. See stores/toastStore.
 -->
<div class="$.root" data-visible={visible}>
  <div class="$.message">{message}</div>
</div>

<style>
  div.root {
    position: absolute;
    display: flex;
    justify-content: center;
    top: 39px;
    left: 0;
    width: 100%;
    opacity: 0;
    transition: opacity 500ms;
  }

  div.root[data-visible='true'] {
    opacity: 0.8;
  }

  div.message {
    justify-content: center;
    background-color: #4caf50;
    color: white;
    padding: 4px 8px;
    font-size: 12px;
    border-radius: 3px;
  }
</style>
